<template>
  <div id="app">
    <!-- <router-link to="/gymlist">Home</router-link> -->
    <router-view></router-view>
  </div>
</template>

<script>
// 主组件
import Vue from 'vue'
export default {
  name: 'App',
  data () {
    return {
      topModelStatus: {
        classname: '',
        show: false,
        bodyhtml: '',
        closehtml: ''
      }
    }
  },
  created () {
    // 创建模态框
    let self = this
    Vue.prototype.createModal = function (obj) {
      self.topModelStatus = obj
    }
    // 刷新token值
    Vue.prototype.refreshToken = function () {
      self.refresh()
    }
    Vue.prototype.fgh = '1'
  },
  mounted () {
  },
  methods: {
    async refresh () {
      if (!localStorage.access_token) return
      let that = this
      let data = {
        refresh_token: localStorage.refresh_token,
        appName: 'FitForceH5'
      }
      const res = await refreshToken(data)
      if (res  && res.access_token) {
        localStorage.setItem('access_token', res.access_token)
        localStorage.setItem('time', res.expires_in)
        localStorage.setItem('refresh_token', res.refresh_token)
        setInterval(_=>{
          that.refreshToken()
        }, (localStorage.time) * 1000)
      } else {
        localStorage.setItem('access_token', '')
        localStorage.setItem('time', '')
        localStorage.setItem('refresh_token', '')
      } 
    }
  }
}
</script>

<style>
#app {
   /* font-family: 'Avenir', Helvetica, Arial, sans-serif; */
  font-family: PingFangSC-Medium;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #8597A1;
  font-size: 0.18rem;
  width:100%;
  height: 100%;
}
ul, li{
	list-style: none;
}
*{
	margin: 0;
	padding: 0;
}
.el-pagination button, .el-pagination span:not{
  line-height: 0.36rem;
}
.el-pager{
  margin: 0 0.1rem;
}
.el-pagination{
  padding: 0;
}
.el-pager .number{
  cursor: pointer!important;
}
.table .is-center  .flexCell img{
  width: 15px;
  height: 14px;
}
.table .is-center  .flexCell{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  white-space: nowrap;
}
.el-radio-button__orig-radio:checked .el-radio-button__inner{
  -webkit-box-shadow: -1px 0 0 0 #20C6BA !important;
  box-shadow: -1px 0 0 0 #20C6BA !important;
}
.el-radio-button.el-radio-button--small.is-active .el-radio-button__inner{
  -webkit-box-shadow: -1px 0 0 0 #20C6BA !important;
  box-shadow: -1px 0 0 0 #20C6BA !important;
}
.el-date-table td.current:not(.disabled) span{
  background: #20C6BA !important;
}
</style>



