import Vue from 'vue'
import Vuex from 'vuex'
import Cookies from 'js-cookie'

Vue.use(Vuex)
// 引用数据状态
const state = {
  token: Cookies.get('token') || '',
  // 续卡的客户
  person: {},
  branchList: []
}

// 使用mutation 来操作state
const mutations = {
  setToken (state, token) {
    Cookies.set('token', token, {expires: 1})
    state.token = token
  },

  // 清楚缓存
  clearCache (state, clear) {
    let cookies = ['token']
    cookies.forEach(v => Cookies.remove(v))
    window.sessionStorage.clear()
    window.localStorage.clear()
  },

  // 获取续卡的信息
  setPerson (state, person) {
    state.person = person
  },

  // 保存分店
  savebranchList (st, param) {
    st.branchList = param.branchList
  }
}

// 通过state来再次过滤数据
const getters = {
  getToken: (state) => state.token
}

export default new Vuex.Store({
  state,
  mutations,
  getters
})



