
export function phoneFormat (str) {
  if (!str) return
  return str.substring(0, 3) + '-' + str.substring(3, 7) + '-' + str.substring(7, 11)
}
export function timeformer (val) {
  if (!val) return
  var d = new Date()
  d.setTime(val)
  // var hour = d.getHours()
  // var minute = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes()
  // var second = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds()
  let y = d.getFullYear()
  let m = (d.getMonth() + 1) < 10 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1) // 获取当前月份的日期，不足10补0
  let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate() // 获取当前几号，不足10补0
  let commonTime = y + '-' + m + '-' + day
  return commonTime
}
// 通过时间戳或者时间
export function getMinuter (val) {
  if (!val) return
  var d = new Date()
  d.setTime(val)
  var hour = d.getHours()
  var minute = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes()
  // var second = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds()
  // let y = d.getFullYear()
  // let m = (d.getMonth() + 1) < 10 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1) // 获取当前月份的日期，不足10补0
  // let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate() // 获取当前几号，不足10补0
  let commonTime = hour + ':' + minute
  return commonTime
}



